/* jshint ignore:start */

if (!runningTests) {
  require("messenger/app")["default"].create({"name":"messenger","version":"0.0.0+88c6401f"});
}

define('~fastboot/app-factory', ['messenger/app', 'messenger/config/environment'], function(App, config) {
  App = App['default'];
  config = config['default'];

  return {
    'default': function() {
      return App.create(config.APP);
    }
  };
});


/* jshint ignore:end */
